function App() {
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    setTimeout(() => setLoading(false), 3000)
  }, [])

  return (
    <>
    {loading === false ? (
      <Router basename={process.env.PUBLIC_URL}>
        <Switch>
          <Route exact path="/" >
            <Home />
          </Route>
          <Route path="/your-email-is-activated" >
            <EmailActivated />
          </Route>
          <Route path="/your-email-is-verified" >
            <EmailVerified />
          </Route>
        </Switch>
      </Router>
    ) : (
      <LoadingScreen />
    )}
    </>
  );
}

export default App;